// 首页商品
let _this = this
import config from '@/api/config.js';
import {request} from '@/api/reques.js'
import {goWindow} from '@/api/navigate.js'  // 页面跳转
export default{
	data(){
		return{
			show:false
		}
	},
	props:{
		options:{
			type:Object,
			default:()=>{
				return {}
			}
		},
		goodsList:{
			type:Array
		},
		mark:{
			type:Boolean
		}
	},
	/* watch:{
		goodsList(arr){
			if(arr.length == 0){
				_this.show = true
			}else{
				_this.show = false
			}
		}
		
	}, */
	created() {
		_this = this;

	},
	filters:{
		filterImg(src){
			if(src){
				return config.domain + src
			}
		}
	},
	methods:{
		// 页面跳转
		handleIndex(goodsId){
			goWindow('/pages/DirectSelling/DirectSelling?id=',goodsId)
		},
		
		handleErr(e,index){
			if(e.type=='error'){
				this.goodsList[index].pic_cover = "../../../static/images/list.jpg"
			}
			
		},
		handleAll(){
			this.$emit("handleAll",true)
		}
	}
}
